import React, { useState, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { useLocation } from "react-router-dom";
import * as S from "./TextBtnStyle";

function BtnText2(number) {
  switch (number) {
    case 1: {
      return "    .";
    }
    case 2: {
      return "   ";
    }
    case 3: {
      return "  ...     .";
    }
    case 4: {
      return "    .";
    }
    case 5: {
      return "      ";
    }
    case 6: {
      return "    ";
    }
    case 7: {
      return "(     ~)";
    }
    case 8: {
      return "   .";
    }
    case 9: {
      return "?  ?  ?";
    }
    case 10: {
      return "   .";
    }
    case 11: {
      return "    ";
    }
    case 12: {
      return "(    ..?)";
    }
    default: {
      return null;
    }
  }
}

function BtnText(number) {
  switch (number) {
    case 1: {
      return "   .";
    }
    case 2: {
      return "    ";
    }
    case 3: {
      return " ...    .";
    }
    case 4: {
      return "   .";
    }
    case 5: {
      return "   !   ";
    }
    case 6: {
      return "    ";
    }
    case 7: {
      return "(..       ..)";
    }
    case 8: {
      return "    .";
    }
    case 9: {
      return "   ?    ? ";
    }
    case 10: {
      return "      .";
    }
    case 11: {
      return "  ?     ?";
    }
    case 12: {
      return "(...    )";
    }
    default: {
      return null;
    }
  }
}

let IE = 0;
let NS = 0;
let FT = 0;
let JP = 0;

const ChoiceBtn = ({ number }) => {
  const location = useLocation();

  const navigate = useNavigate();
  function navigateClick(mbtiword) {
    navigate("/mbti-result", { state: { mbti: mbtiword } });
  }

  const TextBtntop = useMemo(() => BtnText(number), [number]);
  const TextBtnbottom = useMemo(() => BtnText2(number), [number]);
  const [alpha, setAlpha] = useState(null);

  setTimeout(() => setAlpha(number));

  function click(btnid) {
    const btnids = btnid.target.id;

    setTimeout(() => setAlpha(number));

    if (btnids == "topbtn") {
      switch (alpha) {
        case 1: {
          IE += 1;
          break;
        }
        case 2: {
          IE -= 1;
          break;
        }
        case 3: {
          NS += 1;
          break;
        }
        case 4: {
          JP += 1;
          break;
        }
        case 5: {
          FT += 1;
          break;
        }
        case 6: {
          NS += 1;
          break;
        }
        case 7: {
          JP += 1;
          break;
        }
        case 8: {
          NS -= 1;
          break;
        }
        case 9: {
          FT += 1;
          break;
        }
        case 10: {
          IE += 1;
          break;
        }
        case 11: {
          JP += 1;
          break;
        }
        case 12: {
          FT += 1;

          if (IE >= 1) {
            IE = "I";
          } else if (IE <= -1) {
            IE = "E";
          }
          if (NS >= 1) {
            NS = "S";
          } else if (NS <= -1) {
            NS = "N";
          }
          if (FT >= 1) {
            FT = "F";
          } else if (FT <= -1) {
            FT = "T";
          }
          if (JP >= 1) {
            JP = "J";
          } else if (JP <= -1) {
            JP = "P";
          }
          const mbti = [IE + NS + FT + JP];
          console.log(mbti[0]);
          navigateClick(mbti[0]);

          console.log("A = ", IE);
          console.log("B = ", NS);
          console.log("C = ", FT);
          console.log("D = ", JP);
          break;
        }
        default: {
          return null;
        }
      }
    } else if (btnids == "bottombtn") {
      switch (alpha) {
        case 1: {
          IE -= 1;
          break;
        }
        case 2: {
          IE += 1;
          break;
        }
        case 3: {
          NS -= 1;
          break;
        }
        case 4: {
          JP -= 1;
          break;
        }
        case 5: {
          FT -= 1;
          break;
        }
        case 6: {
          NS -= 1;
          break;
        }
        case 7: {
          JP -= 1;
          break;
        }
        case 8: {
          NS += 1;
          break;
        }
        case 9: {
          FT -= 1;
          break;
        }
        case 10: {
          IE -= 1;
          break;
        }
        case 11: {
          JP -= 1;
          break;
        }
        case 12: {
          FT -= 1;

          if (IE >= 1) {
            IE = "I";
          } else if (IE <= -1) {
            IE = "E";
          }
          if (NS >= 1) {
            NS = "S";
          } else if (NS <= -1) {
            NS = "N";
          }
          if (FT >= 1) {
            FT = "F";
          } else if (FT <= -1) {
            FT = "T";
          }
          if (JP >= 1) {
            JP = "J";
          } else if (JP <= -1) {
            JP = "P";
          }
          const mbti = [IE + NS + FT + JP];

          navigateClick(mbti);
          console.log("A = ", IE);
          console.log("B = ", NS);
          console.log("C = ", FT);
          console.log("D = ", JP);
          break;
        }
        default: {
          return null;
        }
      }
    }
  }

  return (
    <>
      <S.Div>
        <S.ChoiceBtn
          onClick={click}
          id="topbtn"
          value="topbtn"
          fontcolor="#ffffff"
          btncolor="#6578a7"
          bordercolor="#6578a7"
        >
          {TextBtntop}
        </S.ChoiceBtn>
      </S.Div>
      <S.Div>
        <S.ChoiceBtn
          onClick={click}
          id="bottombtn"
          value="bottombtn"
          fontcolor="#ffffff"
          btncolor="#6578a7"
          bordercolor="#6578a7"
        >
          {TextBtnbottom}
        </S.ChoiceBtn>
      </S.Div>
    </>
  );
};

export default ChoiceBtn;
